<template>
  <div id="app" :class="{ 'page-home': pageHome }">
    <navigation></navigation>         
    <router-view/>
    <the-footer></the-footer>
  </div>
</template>

<script>
  import Navigation from '@/components/Navigation'
  import TheFooter from '@/components/TheFooter'
  
  export default {
  name: 'app',
  components: {
    Navigation,
    TheFooter
  },
  data () {
    return {
      pageHome: true,
      sticky: true
    }
  },
  watch: {
    // eslint-disable-next-line
    $route (to, from) {
      //console.log('$route to:', to.name)

      if (to.name === 'home') {
        this.pageHome = true
      } else {
        this.pageHome = false
      }
    }
  }
}

</script>

